<template>
  <div class="page trialpreson flex-v trialdoctor">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">初审医生工作量</div>
      <div class="head-btn"
        v-if="btns.hasExport"
        data-btn-id='105003001'
        @click="exportFile">
        <img src="@/assets/img/dsochuIcon.png"
          alt="导出">&nbsp;<span>导出</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        :model="queryObj"
        class="default-form-style"
        size='small'>
        <el-form-item label="登记日期：">
          <el-date-picker value-format='yyyy-MM-dd'
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="机构名称：">
          <el-select v-model="queryObj.createOrganizationId"
            @change="changeOrganization"
            placeholder="全部"
            clearable>
            <el-option :label="item.oname"
              :value="item.sysGuid"
              v-for="item in orgList"
              :key="item.sysGuid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="医生姓名：">
          <el-select v-model="queryObj.regUserName"
            clearable>
            <el-option :label="item.realName"
              :value="item.realName"
              v-for="item in doctorlist"
              :key="item.accountNo"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn "
            icon="el-icon-search"
            round
            @click="onsubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table stripe
        :data="tableData"
        tooltip-effect="dark"
        height='100%'
        v-loading="loading"
        header-cell-class-name='custom-table-cell'>
        <el-table-column type="index"
          width="50"
          label="序号">
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="userName"
          label="姓名">
        </el-table-column>
        <el-table-column align="center"
          prop="personCount"
          label="总人数">
        </el-table-column>
        <el-table-column align="center"
          label="操作">
          <template v-slot="{row}">
            <a class="link"
              v-if="btns.hasMore"
              data-btn-id='105003002'
              @click="goMorePage(row.userId)">
              更多
            </a>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="queryObj"
        query-url="/charts/base/getPrimaryDoctorWorkload"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="loading=$event" />
    </div>
  </div>
</template>
<script>
import { xhrDownload } from '@/lib/xhr'
import { formatDate, exportFile } from '@/lib/utils'
export default {
  name: 'trialdoctor',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      dateRange: '',
      doctorname: '',
      queryObj: {
        regUserName: '',
        startRegisterDate: '',
        endRegisterDate: '',
        doctorType: 1,
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,
      tableData: [],
      doctorlist: [],
      loading: true
    }
  },
  created() {
    this.init()
  },
  methods: {
    exportFile() {
      this.$confirm('', {
        title: '确定导出？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          xhrDownload(
            '/charts/base/exportPrimaryDoctorWorkload',
            this.exportFileParams,
            'arraybuffer'
          )
            .then(({ data }) => {
              exportFile(data, '初审医生工作量')
            })
            .catch(err => this.$message.error(err))
        })
        .catch(cancel => cancel)
    },

    changeOrganization(orgGuid) {
      this.organizationdoctor(orgGuid)
    },

    //查询机构下的医生
    organizationdoctor(orgGuid) {
      let url = '/charts/base/getThisOrgDoctor'
      this.$post(url, { orgGuid })
        .then(({ data }) => {
          this.doctorlist = data.list
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      let { startRegisterDate, endRegisterDate, createOrganizationId } = this.queryObj
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
      this.exportFileParams = {
        pageNum,
        pageSize,
        ...this.queryObj
      }
      this.moreQuery = {
        startRegisterDate,
        endRegisterDate,
        createOrganizationId
      }
    },

    goMorePage(oneAuthUserId) {
      this.$router.push({
        name: 'trialdoctormore',
        query: {
          oneAuthUserId,
          ...this.moreQuery
        }
      })
    },
    onsubmit() {
      this.$refs.paginationEvent.initPageList(1)
    },

    init() {
      let { startRegisterDate, endRegisterDate, createOrganizationId } = this.queryObj
      this.organizationdoctor(createOrganizationId)
      this.moreQuery = {
        startRegisterDate,
        endRegisterDate,
        createOrganizationId
      }
      this.exportFileParams = {
        pageNum: 1,
        pageSize: 10,
        createOrganizationId
      }
    }
  },
  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasExport: ~btnIds.indexOf('105003001'), // 登记按钮权限
        hasMore: ~btnIds.indexOf('105003002') // 修改按钮权限
      }
    },
    orgList() {
      return this.$store.state.orgList
    }
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.queryObj.startRegisterDate = val[0]
        this.queryObj.endRegisterDate = `${val[1]} 23:59:59`
      } else {
        this.queryObj.startRegisterDate = ''
        this.queryObj.endRegisterDate = ''
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.trialdoctor {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      justify-content: center;
      flex-direction: column;
      padding: 0 30px;
      margin-right: 20px;
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
    }
    .countlist-item:last-child {
      margin-right: 20px;
    }
  }
}
</style>

